import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import NavBar from "./../layouts/common/NavBar/NavBar";
import BannerTitle from "./../layouts/common/BannerTitle/BannerTitle";
import MapaSite from "./../layouts/common/MapaSite/MapaSite";
import Footer from "./../layouts/common/Footer/Footer";
import RedesSociais from "./../layouts/common/RedesSociais/RedesSociais";
import Breadcrumb from "./../layouts/common/Breadcrumb/Breadcrumb";
import { DigitoZeroColoredSVG } from "../SVG/DigitoZero";
import PageComponent from "../core/PageComponent";

export default class Empresa extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* Configurção landscape */}
        <div id="lying" />

        {/* Configurção natural */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside empresa">
            <header>
              <NavBar />
            </header>

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Empresa</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Empresa</span>
                  </li>
                </Breadcrumb>

                {/* <div className="master">
                <div className="container">
                  <div className="row">
                    <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <h3> ???? </h3>
                      </div>
                    </div>
                  </div>
                </div>
              </div> */}

                <div className="container">
                  <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <span className="icon logo">
                          <DigitoZeroColoredSVG />
                        </span>
                      </div>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <h2>
                          Somos uma agência de Marketing Digital do futuro que
                          aproxima seu negócio do seu público através do
                          marketing digital e da tecnologia
                        </h2>
                        <p>
                          Nossa missão é oferecer serviços digitais de qualidade
                          às empresas brasileiras, para que elas se relacionem
                          cada vez melhor com seu público-alvo, atraiam
                          consumidores que necessitem seu produto, mas não o
                          conhecem e assim conquistem novos clientes de forma
                          sustentável e progressiva.
                        </p>
                        <p>
                        <strong className="special">Todo mundo ganha</strong> nossos clientes geram novos negócios
                          e atingem os resultados desejados, seus consumidores
                          tem uma melhor experiência e ainda contribuímos para o
                          “novo mercado”, onde o marketing de interrupção dá
                          lugar ao marketing de atração.
                        </p>
                        <h2>O que valorizamos na dígitozero</h2>
                        <p>
                          <strong className="special">Eficiência</strong>
                        </p>
                        <p>
                          <strong className="special">Foco no Cliente</strong>
                        </p>
                        <p>
                          <strong className="special">Qualidade</strong>
                        </p>
                        <p>
                          <strong className="special">Criatividade</strong>
                        </p>
                        <p>
                          <strong className="special">Usabilidade</strong>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="section">
                  <div className="featured">
                    <div className="container">
                      <div className="row">
                        <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 text-center">
                          <h2 className="font-weight-bold">
                            Conheça a nossa equipe
                          </h2>
                          <p>
                            Lorem ipsum dolor sit amet, non odio tincidunt ut
                            ante, lorem a euismod suspendisse vel, sed quam
                            nulla mauris iaculis erat eget vitae.
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                        <div className="nossa-equipe">
                          <div className="row">
                            <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12 pessoa">
                              <div className="wrapper-content">
                                <img src="/img/pessoa.jpg" alt="Pessoa" />
                                <div className="desc-pessoa">
                                  <h5>JOAO PEDRO LEMOS </h5>
                                  CEO
                                </div>
                              </div>
                            </div>
                            <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12 pessoa">
                              <div className="wrapper-content">
                                <img src="/img/pessoa.jpg" alt="Pessoa" />
                                <div className="desc-pessoa">
                                  <h5>LEONARDO AREIAS</h5>
                                  DESIGNER UX
                                </div>
                              </div>
                            </div>
                            <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12 pessoa">
                              <div className="wrapper-content">
                                <img src="/img/pessoa.jpg" alt="Pessoa" />
                                <div className="desc-pessoa">
                                  <h5>LUIZ</h5>
                                  DESENVOLVEDOR
                                </div>
                              </div>
                            </div>
                          </div>
                          <div className="row">
                            <div className="col-xl-4 offset-xl-2 col-lg-4 col-md-12 col-sm-12 col-12 pessoa">
                              <div className="wrapper-content">
                                <img src="/img/pessoa.jpg" alt="Pessoa" />
                                <div className="desc-pessoa">
                                  <h5>MARCELO RODRIGUES</h5>
                                  DESENVOLVEDOR
                                </div>
                              </div>
                            </div>
                            <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12 pessoa">
                              <div className="wrapper-content">
                                <img src="/img/pessoa.jpg" alt="Pessoa" />
                                <div className="desc-pessoa">
                                  <h5>TAYNARA JAEGGER</h5>
                                  DESIGNER DE INTERFACES
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



