import React, { Component } from "react";
import "./NavMainSubItem.sass";
import { Link } from 'react-router-dom';

class NavMainSubItem extends Component {
  render() {
    return (
      <li className="nav-main">
        <Link className="mainSubItem" to={this.props.link}>{this.props.title}</Link>
        <ul>{this.props.children}</ul>
      </li>
    );
  }
}

export default NavMainSubItem;



